      <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
        <Form-item label="角色名称" prop="name">
          <Input v-model="formValidate.name" placeholder="" ></Input>
        </Form-item>
        <Form-item label="角色描述" prop="description">
          <Input v-model="formValidate.description" placeholder="" ></Input>
        </Form-item>
        <Form-item label="是否启用" >
          <i-switch :value="true" size="large" @on-change="getSatueInWrap" style="margin-top:3px;">
            <span slot="open">开启</span>
            <span slot="close">禁用</span>
          </i-switch>
        </Form-item>
        <Form-item label="关联角色" >
          <Tree :data="treeData" show-checkbox multiple    ref="tree"   @on-check-change="treeCheckChange"></Tree>
        </Form-item>
      </Form>
      <div slot="footer">
        <Button @click="addWrap=false">取消</Button>
        <Button  type="primary" :loading="addLoding" @click="addInfoSave">确定</Button>
      </div>
    </Modal>
    <!--修改信息的弹窗-->
    <Modal v-model="updateWrap" title="修改信息"  @on-ok="updateInfoSave" >
      <Form ref="updateFormValidate" :model="updateFormValidate" :rules="ruleValidate" :label-width="70">
        <Form-item label="角色名称" prop="name">
          <Input v-model="updateFormValidate.name" placeholder="" ></Input>
        </Form-item>
        <Form-item label="是否启用" >
          <i-switch v-model="updateFormValidate.statue" size="large" @on-change="getSatueInUpdateWrap" style="margin-top:3px;">
            <span slot="open">开启</span>
            <span slot="close">禁用</span>
          </i-switch>
        </Form-item>
        <Form-item label="关联角色" >
