        </Form-item>
      </Form>
      <div slot="footer">
        <Button @click="updateWrap=false">取消</Button>
        <Button  type="primary" :loading="updateLoading" @click="updateInfoSave">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
/*  import {
    getUserListPage,
    removeUser,
    editUser,
    addUser
  } from '../../../api/api';*/
  import { tableMethods } from "../../../js/mixintable";
  export default {
    data () {
      return {
        /*搜索区域 条件*/
        actorID:'', //角色id
        actorName:'', //角色名称
        permissionSetting:'', //权限设置
        statueSwitch:true,//状态 true false
        creatTime:'', //创建时间
        uptateTime:'', //更新时间
        /*新增弹窗*/
        addWrap:false, //添加弹窗
        addLoding:false,
        treeData: [
          {
            title: '用户管理',
            expand: true,
            selected: true,
            children: [
              {
                title: '管理员管理',
                expand: true,
                children: [
                  {
                    title: '角色管理',
                    disabled: true,

                  },
                  {
                    title: '管理员用户管理',
                    expand: true,
                    children: [
                      {
                        title: '新增',

                      },
                      {
                        title: '修改'
                      },
                      {
                        title: '删除'
                      },
                      {
                        title: '导出'
                      }
                    ]
                  }
                ]
              },
              {
                title: '统计查询',
                expand: true,
                children: [
                  {
                    title: '积分统计查询',
                    checked: true,
                    expand: true,
                    children: [
                      {
                        title: '新增',

                      },
                      {
                        title: '修改'
                      },
                      {
                        title: '删除'
                      },
                      {
                        title: '导出'
                      }
                    ]
                  },
                  {
                    title: '积分互换查询'
                  }
                ]
              }
            ]
          }
        ],
        formValidate:{
          name:'',
          description:'',
          statue:true,
        },
        /*修改弹窗*/
        updateWrap:false, //修改弹窗
        updateLoading:false,
        updateFormValidate:{
          name:'',
          statue:true,
        },
        /*table表格*/
        tableData: [
          {ruleId:'银点acount',ruleName:"银点",creat_time:'2018-2-1',update_time:'2018-2-1',user_power:'power1',"isEnable":'是'},
          {ruleId:'快乐农家acount',ruleName:"快乐农家",creat_time:'2018-2-1',update_time:'2018-2-1',user_power:'power3',"isEnable":'否'},
          {ruleId:'银点acount',ruleName:"银点",creat_time:'2018-2-1',update_time:'2018-2-1',user_power:'power2',"isEnable":'是'}

        ],
        total: 0,
        page: 1,
        tableHeight:200,
        loading:false,
        ruleValidate: {
          name: [
            { required: true, message: '名称不能为空', trigger: 'blur' }
          ],
          description: [
            { required: true, message: '描述不能为空', trigger: 'blur' }
          ],
        },
        tableColumns: [
          {
            title: '角色ID',
            key: 'ruleId',
            align: 'center',
          },
          {
            title: '角色名称',
            align: 'center',
            key: 'ruleName',
            width: 200
          },
          {
            title: '创建时间',
            align: 'center',
            key: 'creat_time'
          },
          {
            title: '更新时间',
            align: 'center',
            key: 'update_time'
          },
          {
            title: '权限设置',
            key: 'user_power',
            align: 'center',
           /* render: function(row, column, index){
              return row.sex == 0 ? '男' : '女';
            }*/
          },
          {
            title: '是否启用',
            align: 'center',
            key: 'isEnable'
          },
          {
            title: '操作',
            key: 'action',
            width: 250,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, '查看'),
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.updateInfo(params)
                    }
                  }
                }, '编辑'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '删除')
              ]);
            }
          }
        ]
      }
    },
    mixins: [tableMethods],
    mounted() {
      this.$nextTick(function () {
        //dom更新了，重新设置table的高度
        //this.tableHeight=this.$refs.tableWrap.clientHeight-this.$refs.searchWrap.clientHeight-this.$refs.handleWrap.clientHeight-this.$refs.pageWrap.clientHeight-3
      })
     // this.mockTableData();
      /*let jsonData = protocol.getJsonParamsBeforeLogin(protocol.Login, {});
      http.ajax('', jsonData, true, (data) => {
        if (data === "0010004") {
          return;
        }
      })*/
    },
    methods: {
      mockTableData () {
        let para = {
          page: this.page
        };
        this.$Loading.start();
        this.loading=true;
        getUserListPage(para).then((res) => {
          this.loading=false;
          this.$Loading.finish();
          this.total = res.data.total;
          this.tableData = res.data.users;
        });
      },
      searchInfo(){
        console.log(this.actorID)
        console.log(this.actorName)
        console.log(this.statueSwitch)
        console.log(this.creatTime)
      },
      show (index) {
        this.$Modal.info({
          title: '用户信息',
          content: `姓名：${this.tableData[index].name}<br>年龄：${this.tableData[index].age}<br>地址：${this.tableData[index].addr}`
        })
      },
      addInfoWrapShow(){
        this.addWrap=true
        this.formValidate={
            name:'',
            description:'',
            statue:true,
        }
      },
      remove (index) {
        let self = this;
        this.$Modal.confirm({
          title: '用户信息',
          content: `是否删除此记录`,
          onOk: function(){
            this.$Loading.start();
            let para = {id: index}
            removeUser(para).then((res)=> {
              self.mockTableData();
            });
          }
        })
      },
      //验证保存添加信息
      addInfoSave() {
        this.addLoding=true
        this.$refs.formValidate.validate((valid) => {
          if (valid) {

            setTimeout(() => {
              this.addWrap=false
              this.addLoding=false
              this.$Message.success('提交成功!');
            }, 2000);
          } else {
            this.$Message.error('表单验证失败!');
            this.addLoding=false
            return false;
          }
        })
      },
      //修改页码 ，重新加载数据
      changePage (row) {
        // 这里直接更改了模拟的数据，真实使用场景应该从服务端获取数据
        this.page = row;
        this.mockTableData();
      },
      //获取到当前的状态
      getSatue(e){
        this.statueSwitch=e
      },
      //获取创建时间
      getCreatTime(e){
        this.creatTime=e
      },
      //获取更新时间
      getUpdateTime(e){
        this.uptateTime=e
      },
      //获取当前新增框中的状态
      getSatueInWrap(e){
        this.formValidate.statue=e
        console.log(this.formValidate)
      },
      /*获取当前已勾选节点的数组*/
      treeCheckChange (e) {
        console.log(e)
       // console.log(this.$refs.tree.getSelectedNodes());
       // console.log(this.$refs.Tree.getCheckedNodes());

      },
      //展示修改弹窗
      updateInfo(data){
        console.log(data)
        this.updateWrap=true
        this.updateFormValidate={
            name:'',
            statue:true,
        }
        //获取该人员的详细信息，并设置值
      },
      //获取修改框中的状态
      getSatueInUpdateWrap(e){
        this.updateFormValidate.statue=e
      },
      //保存修改管理员信息
      updateInfoSave(){
        this.updateLoading=true
        this.$refs.updateFormValidate.validate((valid) => {
          if (valid) {
            this.updateWrap=false
            this.updateLoading=false
            this.$Message.success('提交成功!');
          } else {
            this.$Message.error('表单验证失败!');
            this.updateLoading=false
            return false;
          }
        })
      },
      //导出数据
      exportOk(){
        var form = document.createElement('form')
        form.enctype = 'application/x-www-form-urlencoded'
        form.method = 'POST'
        form.action = baseUrl + '/pc/information/exportInfo.do'
        var map = {
          'token': that.token,
          'strApp': that.strApp,
          'strInfoNo': that.strInfoNo, //
          'isCash': that.isCash, // 全部   是 否
          'strContent': that.strContent, // 内容
        }

        for (var i in map) {
          var f = document.createElement('input')
          f.type = 'hidden'
          f.name = i
          f.value = map[i]
          form.appendChild(f)
        }
        document.body.appendChild(form)
        form.submit()
        document.body.removeChild(form)
        form = null
      },
      //导出数据
      exportInfo(){
        let that=this
        this.$Modal.confirm({
          title: '提示',
          content: `确定导出吗？`,
          onOk: function(){
            that.exportOk()

          }
        })
      },
    },
  }
</script>
<style lang="less" scoped>
  @import '../../../css/common.css';
</style>



